import React from "react";
import { Link } from "react-router-dom";
import "./Footer.scss";
import { Lazy } from "react-lazy";

const Footer = () => {
  const Logo02 = "./assets/logo/logo02.svg";
  return (
    <div className="footer-wrapper">
      <div className="container">
        <Lazy ltIE9 className="logo">
          <img src={Logo02} alt="Logo" />
        </Lazy>
        <div className="footer-row">
          <div className="footer-col">
            <p>
              Sinau tani adalah sebuah website penyedia kursus online gratis,
              yang berbasis di Yogyakarta, Indonesia dan didirikan pada Oktober
              2020
            </p>
            <p>
              Jika anda memiliki ide, kritik atau saran,bisa menghubungi hotline
              kami di :
            </p>
            <Link to="/">0895421900858</Link>
            <Link to="/">sietani@gmail.com</Link>
          </div>
          <div className="footer-col">
            <h5>Menu</h5>
            <Link to="/pertanian">Pertanian</Link>
            <Link to="/hidroponik">Hidroponik</Link>
            <Link to="/perkebunan">perkebunan</Link>
            <Link to="/">Testimoni</Link>
          </div>
          <div className="footer-col">
            <h5>Support dan Donasi</h5>
            <a href="https://saweria.co/usmanhanafi" target="_blank" rel="noreferrer">saweria.com</a>
          </div>
        </div>
      </div>
    </div>
  );
};
export default Footer;